.tutorial {
  position: absolute;
  z-index: 3;
  background-color: rgba(255, 255, 255, 1);
  width: 50%;
  height: 70%;
  border: 2px solid #34495e;
  border-radius: 4px;
  text-align: center;
  float: right;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  white-space: pre-wrap;
  overflow: hidden;
}

.tutorial-hidden {
  visibility: hidden;
  position: absolute;
  z-index: 3;
  background-color: rgba(255, 255, 255, 1);
  width: 50%;
  height: 70%;
  border: 2px solid rgb(12, 53, 71);
  border-radius: 4px;
  text-align: center;
  float: right;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  font-family: "Lato", Helvetica, Arial, sans-serif;
}

.tutorialCounter {
  position: absolute;
  right: 2.5%;
  top: 2.5%;
  font-family: "Lato", Helvetica, Arial, sans-serif;
  font-size: 18px;
  line-height: 1.72222;
  color: #34495e;
}

.tutorial > h3 {
  margin-top: 30px;
  margin-bottom: 0px;
  font-size: 40px;
  font-family: inherit;
  font-weight: 700;
  line-height: 1.1;
  color: inherit;
}

.tutorial > h6 {
  padding: 0px 20px 0px 20px;
  font-size: 24px;
  font-weight: normal;
  margin-bottom: 5px;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1.4em;
          margin-block-end: 1.4em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
}

.tutorial > p {
  font-family: inherit;
  display: block;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  padding: 0px 20px 5px 20px;
  font-size: 18px;
  line-height: 1.72222;
  margin: 0 0 15px;
  white-space: pre-wrap;
}

