/* Mini reset, no margins, paddings or bullets */
.menu,
.submenu {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* Main level */
.menu {
  /* http://www.red-team-design.com/horizontal-centering-using-css-fit-content-value */
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}

.menu > li {
  border-radius: 5px;
  float: left;
  position: relative;
}

.menu a {
  font-family: "Lato", Helvetica, Arial, sans-serif;
  font-weight: 700;
  display: block;
  color: #fff;
  text-decoration: none;
  font-size: 15px;
}

.menu li:hover {
  background: rgb(158, 158, 158);
}

.menu > li > a {
  padding: 1em 2em;
}

.menu > li > a:hover {
  padding: 1em 2em;
}

/* Dropdown */
.submenu {
  z-index: 1;
  position: absolute;
  width: 260px;
  left: 50%;
  margin-left: -115px;
  -webkit-transform-origin: left top;
          transform-origin: left top;
}

.submenu-speed {
  z-index: 1;
  position: absolute;
  width: 200px;
  left: 50%;
  margin-left: -90px;
  -webkit-transform-origin: left top;
          transform-origin: left top;
}

.submenu li,
.submenu-speed li {
  background-color: rgb(12, 53, 71);
  position: relative;
  overflow: hidden;
}

.submenu > li > a,
.submenu-speed > li > a {
  padding: 1em 2em;
}

.submenu > li::after,
.submenu-speed > li::after {
  content: "";
  position: absolute;
  top: -125%;
  height: 100%;
  width: 100%;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.9);
}

/* Odd stuff */
.submenu > li:nth-child(odd),
.submenu-speed > li:nth-child(odd) {
  -webkit-transform: skewX(-25deg) translateX(0);
          transform: skewX(-25deg) translateX(0);
}

.submenu > li:nth-child(odd) > a,
.submenu-speed > li:nth-child(odd) > a {
  -webkit-transform: skewX(25deg);
          transform: skewX(25deg);
}

.submenu > li:nth-child(odd)::after,
.submenu-speed > li:nth-child(odd)::after {
  right: -50%;
  -webkit-transform: skewX(-25deg) rotate(3deg);
          transform: skewX(-25deg) rotate(3deg);
}

/* Even stuff */
.submenu > li:nth-child(even),
.submenu-speed > li:nth-child(even) {
  -webkit-transform: skewX(25deg) translateX(0);
          transform: skewX(25deg) translateX(0);
}

.submenu > li:nth-child(even) > a,
.submenu-speed > li:nth-child(even) > a {
  -webkit-transform: skewX(-25deg);
          transform: skewX(-25deg);
}

.submenu > li:nth-child(even)::after,
.submenu-speed > li:nth-child(even)::after {
  left: -50%;
  -webkit-transform: skewX(25deg) rotate(3deg);
          transform: skewX(25deg) rotate(3deg);
}

/* Show dropdown */
.submenu,
.submenu li,
.submenu-speed,
.submenu-speed li {
  opacity: 0;
  visibility: hidden;
}

.submenu li,
.submenu-speed li {
  transition: 0.2s ease transform;
}

.menu > li:hover .submenu,
.menu > li:hover .submenu li,
.menu > li:hover .submenu-speed,
.menu > li:hover .submenu-speed li {
  opacity: 1;
  visibility: visible;
}

.menu > li:hover .submenu li:nth-child(even),
.menu > li:hover .submenu-speed li:nth-child(even) {
  -webkit-transform: skewX(25deg) translateX(15px);
          transform: skewX(25deg) translateX(15px);
}

.menu > li:hover .submenu li:nth-child(odd),
.menu > li:hover .submenu-speed li:nth-child(odd) {
  -webkit-transform: skewX(-25deg) translateX(-15px);
          transform: skewX(-25deg) translateX(-15px);
}

